@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    display: flex;
    text-align: center;
    background-color: #404653;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    font-family: Raleway;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.SiteHeader {
    height: 7%;
}

.Body {
    height: 88%;
}

.SiteFooter {
    height: 5vh;
}
.SiteHeader {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    font-weight: bolder;
    color: #ffffff;

}

.SiteHeader.Desktop {
    justify-content: space-between;
    font-size: larger;
    width: 80%;
}

.SiteHeader.Mobile {
    justify-content: center;
    font-size: large;
}
.HeaderItem {
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}

.HeaderItem.isMobile {
    height: 50%;
    padding-left: 5px;
    padding-right: 5px;
}

.HeaderItem:hover {
    color: #000000;
    background-color: #bf1e2e;
    cursor: pointer;
}
.Home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #000000;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

.App-link {
    color: #000000;
    font-family: Raleway;
}

a {
    color: #bf1e2e;
}
.SiteFooter {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.FooterItem {
    padding: 3px;
}
.Body {
    display: flex;
}

.Error {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
