.SiteHeader {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    font-weight: bolder;
    color: #ffffff;

}

.SiteHeader.Desktop {
    justify-content: space-between;
    font-size: larger;
    width: 80%;
}

.SiteHeader.Mobile {
    justify-content: center;
    font-size: large;
}